@import "@/assets/styles/_variables.scss";

    .dnd-app {
        width: 100%;
        padding: 40px 0 0;
    }

    .header {
        @include content;

        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 40px;
        z-index: 100;
        background-color: $white;
        display: flex;
        align-items: center;
        padding-top: 8px;
        padding-bottom: 8px;

        &__title {
            width: 100%;
            padding: 0 40px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            font-weight: 600;
            font-size: 18px;
            text-align: center;
        }

        &__home {
            @include css_anim($item: color);

            display: flex;
            position: absolute;
            top: 8px;
            right: 16px;
            left: initial;
            color: $black;
            align-items: center;
            justify-content: center;
            width: 24px;
            height: 24px;
            border-radius: 2px;
            cursor: pointer;

            &:hover {
                @include css_anim();

                color: $lBlack;
            }
        }
    }

    .nav {
        @include css_anim();

        position: fixed;
        top: 40px;
        left: 0;
        width: 100%;
        height: calc(100% - 40px);
        background-color: transparentize($black, .7);
        pointer-events: none;
        opacity: 0;
        z-index: 110;
        display: flex;
        justify-content: flex-start;

        &__body {
            @include css_anim();

            width: 288px;
            height: 100%;
            background-color: $white;
            transform: translateX(-100%);
            display: flex;
            flex-direction: column;
            padding-top: 16px;
            box-shadow: inset 1px 4px 9px -6px transparentize($black, .7);
            cursor: default;
            position: relative;
            overflow: auto;
        }

        &.is-opened {
            opacity: 1;
            pointer-events: auto;
            cursor: pointer;

            .nav__body {
                transform: translateX(0);
            }
        }

        &__btn {
            display: flex;
            position: fixed;
            top: 8px;
            left: 16px;
            color: $black;
            align-items: center;
            justify-content: center;
            width: 24px;
            height: 24px;
            border-radius: 2px;
            z-index: 130;
            cursor: pointer;

            &:before, &:after {
                @include css_anim();

                content: '';
                display: block;
                position: absolute;
                width: 16px;
                height: 2px;
                border-radius: 2px;
                background-color: currentColor;
            }

            &:before {
                transform: translateY(-3px);
            }

            &:after {
                transform: translateY(3px);
            }

            &:hover {
                &:before {
                    transform: translateY(-3px) translateX(3px);
                }

                &:after {
                    transform: translateY(3px) translateX(-3px);
                }
            }

            &.is-opened {
                &:before {
                    transform: rotate(45deg);
                }

                &:after {
                    transform: rotate(-45deg);
                }
            }
        }

        &__item {
            padding: 8px 16px;
            display: flex;
            width: 100%;
            cursor: pointer;
            flex-shrink: 0;
            color: $black;

            &:last-of-type {
                padding-bottom: 24px;
            }

            &.is-active {
                color: $green;
            }
        }

        .icons8 {
            width: 100%;
            border-top: 1px solid $gray;
            padding: 12px 16px;
            font-size: 14px;
            background-color: $white;
            color: $black;
            margin-top: auto;
            position: sticky;
            left: 0;
            bottom: 0;

            a {
                color: $green;

                &:hover {
                    color: $gray;
                }
            }
        }
    }
